
import { Entry } from "@/models/Chapter";
import { Options, Vue } from "vue-class-component";

@Options({
  name: "Subsection",
  props: {
    entry: Object,
  },
})
export default class Subsection extends Vue {
  private entry!: Entry;

  get subsection(): string {
    return this.entry.parameters[0];
  }
}
